import {VdlError} from '../core';

/**
 * Exception thrown when menu trigger doesn't have a valid vdl-menu instance
 * @docs-private
 */
export class VdlMenuMissingError extends VdlError {
  constructor() {
    super(`vdl-menu-trigger: must pass in an vdl-menu instance.

    Example:
      <vdl-menu #menu="vdlMenu"></vdl-menu>
      <button [vdlMenuTriggerFor]="menu"></button>
    `);
  }
}

/**
 * Exception thrown when menu's x-position value isn't valid.
 * In other words, it doesn't match 'before' or 'after'.
 * @docs-private
 */
export class VdlMenuInvalidPositionX extends VdlError {
  constructor() {
    super(`x-position value must be either 'before' or after'.
      Example: <vdl-menu x-position="before" #menu="vdlMenu"></vdl-menu>
    `);
  }
}

/**
 * Exception thrown when menu's y-position value isn't valid.
 * In other words, it doesn't match 'above' or 'below'.
 * @docs-private
 */
export class VdlMenuInvalidPositionY extends VdlError {
  constructor() {
    super(`y-position value must be either 'above' or below'.
      Example: <vdl-menu y-position="above" #menu="vdlMenu"></vdl-menu>
    `);
  }
}
